
.search {
    max-width: 800px;
    &__one {
      display: flex;
      flex-direction: row;
    }
    &__two {
      display: flex;
      flex-direction: column;
    }
label{
  margin:auto;
}
  }
  

.search-form {
    h2 {
      font-size: 1.5rem;
      margin-bottom: 1rem;
    }
  
    .form-group {
      margin-bottom: 1rem;
  
      label {
        font-weight: bold;
      }
  
      input {
        width: 100%;
        padding: 0.5rem;
      }
    }
  
  
    button {
      background-color: #007BFF;
      color: white;
      padding: 0.5rem 1rem;
      border: none;
      cursor: pointer;
    }
  }
  
  .two {
    display: flex;
    flex-direction: column;
  }
